.App {
  justify-items: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body{
  margin: 0;
  padding: 0;
  background-color: #282c34;
}

.filters{
  display: flex;
  width: 100%;
  align-items: center;
  margin: 10px;

  justify-content: center;
  font-size: 1rem;
}

.filter-list{
  width: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.content{
  background: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  padding: 30px;
}

.content h1{
  text-align: center;
  color: #2ccc81;
}

.filter-list input{
  margin: 10px;
  text-align: center;
  border-radius: 10px;
  border: 1px solid gray;
}

.filters #typeDropdown{
  display: flex;
  background-color: #1472dd;
  height: 25px;
  align-items: center;
  justify-items: center;
}

.filters #dropitem:hover{
  background-color: #2ccc81;
  color: white;
}
